.App {
  text-align: center;
  max-width: 728px;
  margin: 0 auto;
}

.App header {
  background: var(--header-bg);
  color: var(--header-color);
  border-bottom: var(--header-border);
  height: 10vh;
  min-height: 50px;
  position: fixed;
  width: 100%;
  max-width: 728px;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 36px;
  box-sizing: border-box;
  border-radius: 0 0 18px 18px;
  transition: 0.2s;
  box-shadow: 0 8px 39px 2px rgba(0, 0, 0, 0.05);
  font-family: "Pacifico", cursive;
  backdrop-filter: blur(10px);
}
.logo {
}
@media (max-width: 330px) {
  .logo {
    font-size: 1.5rem;
  }
  .App header {
    padding: 24px;
  }
}
.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  background-color: var(--main-bg);
  border-radius: 0 0 12px 12px;
}
.main-con {
  position: fixed;
  top: 0;
  left: 0;
  width: calc(100% - 20px);
  /* padding-bottom: 10vh; */
  /* height: 90vh; */
}
main {
  width: 100%;
  max-width: 728px;
  margin: 0 auto;
  padding: 10px;
  height: calc(90vh - 22px) !important;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}
@media (min-width: 728px) {
  main {
    transform: translateX(10px);
  }
}

main::-webkit-scrollbar {
  width: 0.25rem;
}

main::-webkit-scrollbar-track {
  background: transparent;
}

main::-webkit-scrollbar-thumb {
  background: transparent;
}

.send-btn {
  position: absolute;
  top: 3px;
  right: 2px;
  height: calc(10vh - 16px);
  margin: 8px;
}
.form-con {
  position: fixed;
  bottom: 0;
  height: 10vh;
  width: 100%;
  padding: 8px;
}
form {
  position: relative;
  width: calc(100% - 16px);
  max-width: calc(728px - 16px);
}

form button {
  width: 20%;
  background-color: var(--send-btn-bg);
}

input {
  line-height: 1.5;
  width: calc(100% - 42px);
  max-width: 728px;
  height: 10vh;
  font-size: 1.2rem;
  background: var(--input-bg);
  background: transparent;
  color: var(--input-color);
  outline: none;
  border: none;
  padding: 0 18px;
  border-radius: 12px;
  border: var(--border-input);
  transition: 0.2s;
  background-color: var(--main-bg);
  font-family: "Ubuntu", sans-serif;
}
input:focus {
  border: 3px solid rgba(0, 68, 255, 0.5);
}

button {
  background-color: var(--btn-bg);
  border: none;
  color: white;
  padding: 12px 26px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.15rem;
  border-radius: 12px;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
@media (max-width: 600px) {
  button {
    padding: 12px 16px;
  }
}
@media (max-width: 400px) {
  button {
    padding: 12px;
  }
}
.sign-in {
  color: #282c34;
  background: rgb(255, 255, 255);
  max-width: 400px;
  margin: 0 auto;
}
.sign-out {
  background-color: rgb(194, 0, 0);
  font-size: 0.8rem;
}

ul,
li {
  text-align: left;
  list-style: none;
}

p {
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
  font-size: 1rem;
  color: var(--text-color);
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
}
.message:first-child {
  margin-top: 10vh;
}

.message:last-child {
  margin-bottom: 200vh;
}

.sent {
  flex-direction: row-reverse;
}

.sent p {
  align-self: flex-end;
  word-wrap: break-word;
  text-align: left;
  border: 2px solid transparent;
  background-color: var(--msg-bg-right);
  border: var(--msg-border-right);
  max-width: 60%;
}
.received p {
  word-wrap: break-word;
  max-width: 60%;
  text-align: left;
  border: 2px solid #cacaca25;
  background-color: var(--msg-bg-left);
  border: var(--msg-border-left);
}

img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin: 2px 5px;
}
.theme-btn {
  background-color: var(--theme-btn-bg);
  color: var(--theme-btn-color);
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
}
