@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Flow+Circular&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-position: fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.theme-light {
  --global-bg: black;
  --bg-color: #ffffff;
  --text-color: rgb(38, 38, 43);
  --send-btn-bg: rgb(0, 89, 255);

  --input-bg: white;
  --input-color: black;
  --border-input: 3px solid rgba(0, 0, 0, 0.05);

  --main-bg: rgb(244, 246, 248);

  --header-color: rgb(44, 44, 56);

  --btn-bg: rgba(0, 0, 0, 0.8);

  /* Msg  */
  --msg-color-right: black;
  --msg-color-left: black;

  --msg-bg-right: rgba(0, 0, 0, 0.03);
  --msg-bg-left: transparent;

  --msg-border-right: 2px solid transparent;
  --msg-border-left: 2px solid var(--msg-bg-right);

  --theme-btn-bg: white;
  --theme-btn-color: rgb(43, 43, 43);
}
.theme-dark {
  --global-bg: black;
  --main-bg: rgb(29, 31, 32);
  --bg-color: #1e1e20;
  --text-color: rgb(209, 212, 221);
  --send-btn-bg: rgb(140, 0, 255);

  --input-bg: var(--main-bg);
  --input-color: rgb(255, 255, 255);
  --border-input: 3px solid var(--header-bg);

  --header-bg: rgba(33, 37, 41, 0.103);
  --header-color: rgb(216, 216, 216);

  --btn-bg: rgba(0, 0, 0, 0.8);

  /* Msg  */
  --msg-color-right: black;
  --msg-color-left: black;

  --msg-bg-right: rgba(255, 255, 255, 0.03);
  --msg-bg-left: transparent;

  --msg-border-right: 2px solid transparent;
  --msg-border-left: 2px solid var(--msg-bg-right);
  --theme-btn-bg: rgb(22, 22, 22);
  --theme-btn-color: rgb(255, 136, 0);
}

.blank {
  height: 10vh;
  width: 100%;
  background-color: transparent !important;
}

.App {
  text-align: center;
  max-width: 728px;
  margin: 0 auto;
}

.App header {
  background: var(--header-bg);
  color: var(--header-color);
  border-bottom: var(--header-border);
  height: 10vh;
  min-height: 50px;
  position: fixed;
  width: 100%;
  max-width: 728px;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  padding: 36px;
  box-sizing: border-box;
  border-radius: 0 0 18px 18px;
  transition: 0.2s;
  box-shadow: 0 8px 39px 2px rgba(0, 0, 0, 0.05);
  font-family: "Pacifico", cursive;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.logo {
}
@media (max-width: 330px) {
  .logo {
    font-size: 1.5rem;
  }
  .App header {
    padding: 24px;
  }
}
.App section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  background-color: var(--main-bg);
  border-radius: 0 0 12px 12px;
}
.main-con {
  position: fixed;
  top: 0;
  left: 0;
  width: calc(100% - 20px);
  /* padding-bottom: 10vh; */
  /* height: 90vh; */
}
main {
  width: 100%;
  max-width: 728px;
  margin: 0 auto;
  padding: 10px;
  height: calc(90vh - 22px) !important;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}
@media (min-width: 728px) {
  main {
    transform: translateX(10px);
  }
}

main::-webkit-scrollbar {
  width: 0.25rem;
}

main::-webkit-scrollbar-track {
  background: transparent;
}

main::-webkit-scrollbar-thumb {
  background: transparent;
}

.send-btn {
  position: absolute;
  top: 3px;
  right: 2px;
  height: calc(10vh - 16px);
  margin: 8px;
}
.form-con {
  position: fixed;
  bottom: 0;
  height: 10vh;
  width: 100%;
  padding: 8px;
}
form {
  position: relative;
  width: calc(100% - 16px);
  max-width: calc(728px - 16px);
}

form button {
  width: 20%;
  background-color: var(--send-btn-bg);
}

input {
  line-height: 1.5;
  width: calc(100% - 42px);
  max-width: 728px;
  height: 10vh;
  font-size: 1.2rem;
  background: var(--input-bg);
  background: transparent;
  color: var(--input-color);
  outline: none;
  border: none;
  padding: 0 18px;
  border-radius: 12px;
  border: var(--border-input);
  transition: 0.2s;
  background-color: var(--main-bg);
  font-family: "Ubuntu", sans-serif;
}
input:focus {
  border: 3px solid rgba(0, 68, 255, 0.5);
}

button {
  background-color: var(--btn-bg);
  border: none;
  color: white;
  padding: 12px 26px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: 1.15rem;
  border-radius: 12px;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
@media (max-width: 600px) {
  button {
    padding: 12px 16px;
  }
}
@media (max-width: 400px) {
  button {
    padding: 12px;
  }
}
.sign-in {
  color: #282c34;
  background: rgb(255, 255, 255);
  max-width: 400px;
  margin: 0 auto;
}
.sign-out {
  background-color: rgb(194, 0, 0);
  font-size: 0.8rem;
}

ul,
li {
  text-align: left;
  list-style: none;
}

p {
  max-width: 500px;
  margin-bottom: 12px;
  line-height: 24px;
  padding: 10px 20px;
  border-radius: 25px;
  position: relative;
  text-align: center;
}

.message {
  display: flex;
  align-items: center;
  font-size: 1rem;
  color: var(--text-color);
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
}
.message:first-child {
  margin-top: 10vh;
}

.message:last-child {
  margin-bottom: 200vh;
}

.sent {
  flex-direction: row-reverse;
}

.sent p {
  align-self: flex-end;
  word-wrap: break-word;
  text-align: left;
  border: 2px solid transparent;
  background-color: var(--msg-bg-right);
  border: var(--msg-border-right);
  max-width: 60%;
}
.received p {
  word-wrap: break-word;
  max-width: 60%;
  text-align: left;
  border: 2px solid #cacaca25;
  background-color: var(--msg-bg-left);
  border: var(--msg-border-left);
}

img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin: 2px 5px;
}
.theme-btn {
  background-color: var(--theme-btn-bg);
  color: var(--theme-btn-color);
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
}

